// 
export const weappPay = (option) => {
  return new Promise((resolve, reject) => {
    // 
    wx.requestPayment({
      ...option,
      timeStamp: option.timeStamp + '',
      success: (success) => {
        uni.showToast({
          title: '', icon: 'success', duration: 2000
        });
        resolve(success)
      },
      fail: (error) => {
        uni.showToast({ title: '', icon: 'none', duration: 2000 });
        reject(error)
      }
    })
  })
}
